._card {
  overflow: hidden;
  max-height: 400px;
  width: 225px;
}
._card:hover {
  cursor: pointer;
}
._body {
  background: #333;
  color: white;
  z-index: 1;
  height: 100px;
}
._bar {
  width: 100%;
  height: 4px;
  background: #dc3545;
}
._card ._img {
  width: 100%;
  transform: scale(1);
  overflow: hidden;
  transition: transform 0.2s ease-out;
}
._card:hover ._img {
  transform: scale(1.1);
}
._card:hover ._body {
  background: #dc3545;
  color: white;
}

._card:hover .title {
  text-decoration: underline;
}
