@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:700,700i);
body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  height: 100%;
  font-size: 13px;
}
body h2 {
  font-size: 30px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.loader {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.title {
  font-size: 1.2rem;
}

._title {
  height: 40vh;
  width: 100vw;
  background: #333;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  position: relative;
  margin-bottom: 100px;
}

._title::after {
  content: "";
  position: absolute;
  top: calc(40vh - 10px);
  width: 105vw;
  height: 110px;
  background: #333;
  z-index: -1;
  -webkit-clip-path: polygon(0 100%, 100% 10px, 100% 0, 0 0);
          clip-path: polygon(0 100%, 100% 10px, 100% 0, 0 0);
}

._title .image {
  max-width: 70%;
  justify-self: end;
  align-self: center;
  grid-column-start: 1;
  grid-column-end: 2;
}

._title .name {
  color: #fff;
  font-family: "Roboto Condensed", sans-serif;
  justify-self: start;
  align-self: center;
  grid-column-start: 2;
  grid-column-end: 4;
}

._title-bars {
  position: relative;
  line-height: 1;
  margin-bottom: 15px;
}

._title-bars::before {
  background-color: #c6a972;
  content: "";
  display: block;
  height: 2px;
  position: absolute;
  -webkit-transform: rotate(-45deg) skew(45deg);
          transform: rotate(-45deg) skew(45deg);
  width: 30px;
  left: 50px;
  top: 1px;
  -webkit-transform-origin: left bottom;
          transform-origin: left bottom;
}

._title-bars::after {
  background-color: #c6a972;
  content: "";
  display: block;
  height: 2px;
  position: absolute;
  -webkit-transform: rotate(-45deg) skew(45deg);
          transform: rotate(-45deg) skew(45deg);
  width: 30px;
  left: -25px;
  top: calc(90% + 1px);
  -webkit-transform-origin: right top;
          transform-origin: right top;
}

@media (max-width: 480px) {
  ._title {
    height: 45vh;
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: 1fr;
  }
  ._title::after {
    top: calc(45vh - 10px);
    height: 60px;
  }
  ._title .image {
    margin-top: 10px;
    width: 40%;
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 2;
    align-self: center;
    justify-self: center;
  }
  ._title .name {
    align-self: center;
    justify-self: center;
    grid-row-start: 2;
    grid-row-end: 3;
    grid-column-start: 1;
    grid-column-end: 2;
  }
  ._title-bars {
    text-align: center;
  }
  ._title-bars::before {
    left: 60%;
    top: 0%;
  }
  ._title-bars::after {
    left: 32%;
    top: 90%;
  }
}

nav {
  padding: 0 !important;
}
.navbar {
  background-color: #333;
}
.brand {
  height: 40px;
  text-align: center;
  padding: 0;
  margin: 0;
}
.img {
  height: 40px;
}

.input_group {
  width: 40%;
}
.search {
  border: none;
  border-bottom: 1px solid #333;
  border-radius: 0;
}

.search::-webkit-input-placeholder {
  font-size: 1rem;
  text-transform: uppercase;
}

.search::-ms-input-placeholder {
  font-size: 1rem;
  text-transform: uppercase;
}

.search::placeholder {
  font-size: 1rem;
  text-transform: uppercase;
}

.icon {
  position: relative;
  top: 10px;
  left: -5px;
}

@media (max-width: 480px) {
  .input_group {
    width: 80%;
  }
}

._card {
  overflow: hidden;
  max-height: 400px;
  width: 225px;
}
._card:hover {
  cursor: pointer;
}
._body {
  background: #333;
  color: white;
  z-index: 1;
  height: 100px;
}
._bar {
  width: 100%;
  height: 4px;
  background: #dc3545;
}
._card ._img {
  width: 100%;
  -webkit-transform: scale(1);
          transform: scale(1);
  overflow: hidden;
  transition: -webkit-transform 0.2s ease-out;
  transition: transform 0.2s ease-out;
  transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
}
._card:hover ._img {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
._card:hover ._body {
  background: #dc3545;
  color: white;
}

._card:hover .title {
  text-decoration: underline;
}

.poster {
  width: 100vw;
  height: 50vh;
  position: relative;
  color: #fff;
  background-image: url(/static/media/poster.d9e113f2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}
.sheet {
  width: 100vw;
  height: 50vh;
  background: #333;
  opacity: 0.8;
  position: absolute;
  top: 0;
  left: 0;
}

.poster_title {
  position: absolute;
  top: 40%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #fff;
}
.poster_title h2 {
  text-align: center;
  font-size: 2.5rem;
}
.poster_title p {
  width: 70vw;
  font-size: 13px;
  text-align: center;
}

